import React from 'react';
import { Image } from 'react-konva';
import {cover, contain} from 'intrinsic-scale';


// custom component that will handle loading image from url
// you may add more logic here to handle "loading" state
// or if loading is failed
// VERY IMPORTANT NOTES:
// at first we will set image state to null
// and then we will set it to native image instance when it is loaded
export class BackgroundImage extends React.Component {
  state = {
    image: null
  };
  
  componentDidMount() {
    this.loadImage();
  }
  componentDidUpdate(oldProps) {
    if (oldProps.src !== this.props.src) {
      this.loadImage();
    }
  }
  componentWillUnmount() {
    this.image.removeEventListener('load', this.handleLoad);
  }
  loadImage() {
    // save to "this" to remove "load" handler on unmount
    this.image = new window.Image();
    this.image.src = this.props.src;
    let width = this.width;
    let height = this.height;

    /* Step 1 - Get the ratio of the div + the image */
    let imageRatio = width/height;
    let coverRatio = this.image.outerWidth/this.image.outerHeight;
    let coverWidth, coverHeight, scale;
    /* Step 2 - Work out which ratio is greater */
    if (imageRatio >= coverRatio) {
        /* The Height is our constant */
        coverHeight = this.image.outerHeight();
        scale = (coverHeight / height);
        coverWidth = width * scale;
    } else {
        /* The Width is our constant */
        coverWidth = this.image.outerWidth();
        scale = (coverWidth / width);
        coverHeight = height * scale;
    }
    let cover = coverWidth + 'px ' + coverHeight + 'px';
    console.log('scale: ' + scale + ', width: ' + coverWidth + ', height: ' + coverHeight + ', cover property: ' + cover);

    this.image.addEventListener('load', this.handleLoad);
    console.log(this.image)
  }
  handleLoad = () => {
    // after setState react-konva will update canvas and redraw the layer
    // because "image" property is changed
    this.setState({
      image: this.image
    });
    // if you keep same image this.image during source updates
    // you will have to update layer manually:
    // this.imageNode.getLayer().batchDraw();
  };
  render() {
    const canvasWidth = window.innerWidth;
    const canvasHeight = window.innerHeight;
    // let { width, height} = cover(canvasWidth, canvasHeight, 0, 0);
    // let { width: bwidth, height: bheight } = cover(100, 200, 50, 50);

    // console.log( canvasWidth, canvasHeight)
    // console.log( width, height)
    // console.log( bwidth, bheight)

    return (
      <Image
        x={this.props.x}
        y={this.props.y}
        image={this.state.image}
        ref={node => {
          this.imageNode = node;
        }}
      />
    );
  }
}
